<script>
import {
  mapState, mapGetters, mapMutations, mapActions,
} from 'vuex';

import Header from '@/components/Header';
import PaymentRequestTable from '@/components/payment_request/PaymentRequestTable';

export default {
  name: 'PaymentRequest',
  components: {
    PaymentRequestTable,
    Header,
  },
  data: () => ({
    showPreloader: false,
  }),
  computed: {
    limit: {
      get() {
        return this.$store.state.paymentRequests.limit;
      },
      set(newValue) {
        this.setLimit(newValue);
        this.setPage(1);
        this.getPaymentRequests();
      },
    },
    page: {
      get() {
        return this.$store.state.paymentRequests.page;
      },
      set(newValue) {
        this.setPage(newValue);
        this.getPaymentRequests();
      },
    },
    ...mapGetters('paymentRequests', ['firstRecord', 'lastRecord']),
    ...mapState('paymentRequests', ['paymentRequests', 'fullCount', 'totalPages']),
  },
  mounted() {
    this.getPaymentRequests();
  },
  methods: {
    ...mapMutations('paymentRequests', ['setLimit', 'setPage']),
    ...mapActions('paymentRequests', ['getPaymentRequests']),
  },
};
</script>
